                  <!--<li class="news-tabs-li news-txt-li show-line-1"><i class="list-style-disc"></i></li>
                                    <li class="news-tabs-li news-txt-li show-line-1"><i class="list-style-disc"></i></li>
                                    <li class="news-tabs-li news-txt-li show-line-1"><i class="list-style-disc"></i></li>
                                    <li class="news-tabs-li news-txt-li show-line-1"><i class="list-style-disc"></i></li>
                                    <li class="news-tabs-li news-txt-li show-line-1"><i class="list-style-disc"></i></li>
                                    <li class="news-tabs-li news-txt-li show-line-1"><i class="list-style-disc"></i></li>
                                    <li class="news-tabs-li news-txt-li show-line-1"><i class="list-style-disc"></i></li>-->
                </ul>
              </div>
            </div>
            <div class="news-body" v-if="!sectiondatas.section7.sectionlist.length">
              <Nodatawithpic></Nodatawithpic>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import api from '../api/api'
  export default{
    name: 'Home',
    data() {
      return {
        bannerdata: [],
        hotdata: [],
        advertdata: [],
        navdatas: [],
        sectiondatas: {
          section1: {
            curtab: 0,
            navdata: [],
            sectionlist: []
          },
          section2: {
            curtab: 0,
            navdata: [],
            sectionlist: []
          },
          section3: {
            curtab: 0,
            navdata: [],
            sectionlist: []
          },
          section4: {
            curtab: 0,
            navdata: [],
            sectionlist: []
          },
          section5: {
            curtab: 0,
            navdata: [],
            sectionlist: []
          },
          section6: {
            curtab: 0,
            navdata: [],
            sectionlist: []
          },
          section7: {
            curtab: 0,
            navdata: [],
            sectionlist: []
          }
        }
      }
    },
    created() {
      // const {
      //   data
      // } = this.$store.state.articlenav
      //
      // this.navdatas = data
      this.start()
      this.requestarticlenav().then(()=>{
        this.initnav()
        this.getsectionlist(1)
        this.getsectionlist(2)
        this.getsectionlist(3)
        this.getsectionlist(4)
        this.getsectionlist(5)
        this.getsectionlist(6)
        this.getsectionlist(7)
        // console.log(this.sectiondatas)
      }).catch(err=>{})
      // this.sectiondatas.section1 = this.getsectionnav(0)
    },
    methods: {
      start() {
        this.requestbanner()
        this.requesthotrrticle()
        this.requestadvertlist()
      },
      initnav() {
        this.sectiondatas.section1.navdata = this.getsectionnav(0)
        this.sectiondatas.section2.navdata = this.getsectionnav(1)
        this.sectiondatas.section3.navdata = this.getsectionnav(2)
        this.sectiondatas.section4.navdata = this.getsectionnav(3)
        this.sectiondatas.section5.navdata = this.getsectionnav(4)
        this.sectiondatas.section6.navdata = this.getsectionnav(5)
        this.sectiondatas.section7.navdata = this.getsectionnav(6)

        for(let i=1;i<=this.navdatas.length;i++){
          this.sectiondatas['section'+i].navitem = this.navdatas[i-1]
        }
      },
      requestbanner() {
        let _this = this
        api.requestbanner().then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            _this.bannerdata = res.data || []
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
      bannerLinker(item) {
        let _this = this
        if(item.attr === 'outer'){
          window.location.href = item.url
        }else if(item.attr === 'inner'){
          this.$router.push({path: '/newsdetails', query: {id: item.article_id}})
        }
      },
      requesthotrrticle(page, num) {
        let _this = this
        api.requesthotrrticle({
          page: page,
          num: num
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            _this.hotdata = res.data || [];
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
      requestadvertlist(page, num) {
        let _this = this
        api.requestadvertlist({
          page: page,
          num: num
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            _this.advertdata = res.data || [];
          }
        }).catch(err=>{
          console.log('err', err)
        })
      },
      advertdetailslinker(item) {
        this.$router.push({path: '/advertdetails', query: {id: item.id}})
      },
      requestarticlenav() {
        let promise = new Promise((resolve, reject)=>{
          api.requestarticlenav().then(response=>{
            return response.data
          }).then(res=>{
            if(res.code === 1){
              this.navdatas = res.data||[]
              resolve()
            }
          }).catch(err=>{})
        })
        return promise
      },
      getsectionnav(index) {
        let navdata = this.navdatas[index]
        let sectionnav = []
        if(navdata.son_nav.length>0){
          sectionnav = navdata.son_nav
        }else{
          sectionnav = [navdata]
        }
        return sectionnav
      },
      getsectionlist(sectionid) {
        let navigate_id = this.sectiondatas['section'+sectionid].navdata[this.sectiondatas['section'+sectionid].curtab].id
        api.requestarticlelist({
          navigate_id: navigate_id,
        }).then(response=>{
          return response.data
        }).then(res=>{
          if(res.code === 1){
            let sectionlist = res.data.article||[]
            this.sectiondatas['section'+sectionid].sectionlist = sectionlist
            // console.log('sectionlist',sectionlist)
            return sectionlist
          }
        }).catch(err=>{})
      },
      switchSectionTab(sectionid, curtab) { //tab
        if(this.sectiondatas['section'+sectionid].navdata.length>1&&this.sectiondatas['section'+sectionid].curtab!==curtab){
          this.sectiondatas['section'+sectionid].curtab = curtab
          this.getsectionlist(sectionid)
        }
      },
      pagemore(sectionid) { //
        let navlen = this.sectiondatas['section'+sectionid].navdata.length

        if(navlen>1){
          this.$router.push({path: '/questions', query: {
            pid: this.sectiondatas['section'+sectionid].navitem.id,
            id: this.sectiondatas['section'+sectionid].navdata[this.sectiondatas['section'+sectionid].curtab].id,
            title: this.sectiondatas['section'+sectionid].navitem.title
            // sectiondata: JSON.stringify(this.sectiondatas['section'+sectionid])
          }})
        }else{
          this.$router.push({path: '/qustions', query: {
              pid: this.sectiondatas['section'+sectionid].navitem.id,
              title: this.sectiondatas['section'+sectionid].navitem.title
              // sectiondata: JSON.stringify(this.sectiondatas['section'+sectionid])
            }})
        }
      },
      toNewsDetails(item) {
        // console.log('item', item)
        this.$router.push({path: '/newsdetails', query: {id: item.id}})
      }
    }
  }
</script>

<style scoped lang="less">
  .page-index{
    background: #FFFFFF;
    padding-bottom: 80px;
    .el-button.is-round{
      padding: 0 .2rem;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }
    /deep/.pic-nodata{
      width: 120px;
    }
  }
  .side-top{
    width: 1200px;
    margin: 0 auto;
    font-size: 0;
    line-height: 0;
    letter-spacing: -4px;
  }
  .index-card-item{
    width: 320px;
    margin-top: 20px;
    position: relative;
    letter-spacing: 0;
  }
  .index-card-bg{
    width: 100%;
    height: auto;
  }
  .index-card-body{
    width: 55%;
    position: absolute;
    top: 15px;
    z-index: 1;
  }
  .index-card-body.left{
    left: 20px;
    text-align: left;
  }
  .index-card-body.right{
    right: 20px;
    text-align: right;
  }
  .index-card-title{
    width: 100%;
    height: 36px;
    font-size: 24px;
    color: #FFFFFF;
    line-height: 36px;
    font-weight: bold;
  }
  .index-card-body.left .index-card-title{
    text-align: left;
  }
  .index-card-body.right .index-card-title{
    text-align: right;
  }
  .index-card-txt{
    width: 100%;
    height: 60px;
    font-size: 16px;
    color: #FFFFFF;
    line-height: 30px;
  }
  .index-card-btn{
    width: 150px;
    padding: 0 10px;
    font-size: 18px;
    text-align: center;
    line-height: 40px;
    font-weight: bold;
    margin-top: 10px;
    border: none;
    background: #FFFFFF;
  }
  .index-card-item.child1 .index-card-btn{
    /*background: #75caaf;*/
    color: #BDECDE;
  }
  .index-card-item.child2 .index-card-btn{
    /*background: #cac378;*/
    color: #EBE7BE;
  }
  .index-card-item.child3 .index-card-btn{
    /*background: #63c0d6;*/
    color: #82E3F9;
  }
  .index-card-item.child4 .index-card-btn{
    /*background: #e08b94;*/
    color: #FFB5BC;
  }
  .index-banner{
    width: 100%;
    margin-top: 20px;
  }
  .hot-view{
    width: 100%;
    margin-top: 6.3px;
    letter-spacing: 0;
  }
  .hot-title{
    width: 100%;
    max-height: 48px;
    font-size: 24px;
    color: #333333;
    line-height: 48px;
    font-weight: bold;
  }
  .hot-row{
    width: 100%;
    height: 20px;
    font-size: 14px;
    color: #666666;
    line-height: 20px;
    font-weight: 500;
    vertical-align: middle;
  }
  .hot-row:hover{
    color: #0084FF;
  }
  .label-block{
    padding: 0 5px;
    box-sizing: border-box;
    background: #4BC1B7;
    position: relative;
    font-size: 14px;
    color: #FFFFFF;
    line-height: 20px;
    font-weight: 500;
    display: inline-block;
    height: 20px;
    white-space: nowrap;
    margin-right: 18px;
    vertical-align: middle;
  }
  .shape-trangle{
    width: 0;
    height: 0;
    border-width: 10px;
    border-style: solid;
    border-color:transparent transparent transparent #4BC1B7;
    position: absolute;
    top: 0;
    right: -20px;
    z-index: 1;
  }
  .side-middle{
    width: 1200px;
    margin: 0 auto;
  }
  .side-bottom{
    width: 1200px;
    margin: 0 auto;
  }
</style>



